.user {
    width: 100%;
    height: 150px;
    background-color: #13CE66;
    border-radius: 20px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 0 10px;
    position: relative;
    margin-top: 20px;
}

.user__img {
    width: 80px;
    height: 80px;
    border-radius: 50%;
}

.user__btn {
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    width: 200px;
    height: 50px;
    background-color: #FFC82C;
    border: none;
}

