.button__box {
    position: absolute;
    top: 0;
    left: 50%;
    transform: translateX(-50%);
    display: flex;
    gap: 20px;
    overflow-x: scroll;
    width: 800px;
    height: 50px;
    padding: 0 10px;
}

.pagination__button {
    background: transparent;
    border: none;
    position: relative;
}

.pagination__button::before {
    content: '';
    width: 25px;
    position: absolute;
    height: 25px;
    border: 1px solid #13CE66;
    border-radius: 50%;
    left: 50%;
    top: 50%;
    transform: translate(-50%,-50%);
}

.pagination__button:hover::before {
    border-color: #FFC82C;
}

.pagination__button--active::before {
    border-color: #FFC82C;
}